import React, { Component } from 'react';
import ParkingListItem from './ParkingListItem';

class ParkingList extends Component {
  render() {
    if (this.props.shortlists.length < 1) return;
    return (
      <div className="container-lg px-0 pb-2">
        <ul className="list-group list-group-flush fs-5">
          <ParkingListItem show={this.props.show} detailIndex={0} carparkData={(this.props.shortlists.length > 0) ? this.props.shortlists[0] : null} />
          <ParkingListItem show={this.props.show} detailIndex={1} carparkData={(this.props.shortlists.length > 1) ? this.props.shortlists[1] : null} />
          <ParkingListItem show={this.props.show} detailIndex={2} carparkData={(this.props.shortlists.length > 2) ? this.props.shortlists[2] : null} />
          <ParkingListItem show={this.props.show} detailIndex={3} carparkData={(this.props.shortlists.length > 3) ? this.props.shortlists[3] : null} />
          <ParkingListItem show={this.props.show} detailIndex={4} carparkData={(this.props.shortlists.length > 4) ? this.props.shortlists[4] : null} />
        </ul>
      </div>
    );
  }
}

export default ParkingList;